<template>
  <div name="register-page" class="register-page" style="background-color: white; height: auto; text-align: left;">
    <div class="container" style="color: black; text-align: center; font-weight: bold; line-height: 1;">
      <span style="font-size: 36px;">ชวนคุณทำแบบสอบถาม</span><br>
      <span style="font-size: 24px;">เพื่อลุ้นรับผลิตภัณฑ์ เอนชัวร์ โกล์ กลิ่นธัญพืช ขนาด 400 กรัม</span><br>
      <span style="font-size: 24px; color: #0281b6;">มูลค่ารางวัลละ 549 บาท จำนวน 20 รางวัล</span><br>
      <span style="font-size: 16px;">ประกาศรายชื่อผู้ได้รับรางวัลที่หน้าไทม์ไลน์ของ Abbott Nutrition TH<br>ในวันที่ 10 ม.ค. 66</span>
    </div>
    <div class="container" style="color: black;">
      <div class="row no-gutters">
        <div class="col-5">
          <label> ชื่อ (Name)<abbr class="slds-required" title="required">* </abbr></label>
        </div>
        <div class="col-7">
          <input-text-std
            v-model="regisData.Name__c"
            v-validate="'required'"
            data-vv-as="ชื่อ (Name)"
            :error="errors.has('Name__c')"
            name="Name__c"
          >
            <template #error>
              <span>{{ errors.first('Name__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-5">
          <label>นามสกุล (Surname)<abbr class="slds-required" title="required">* </abbr></label>
        </div>
        <div class="col-7">
          <input-text-std
            v-model="regisData.Surname__c"
            v-validate="'required'"
            data-vv-as="นามสกุล (Surname)"
            :error="errors.has('Surname__c')"
            name="Surname__c"
            >
            <template #error>
              <span>{{ errors.first('Surname__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>
      
      <div class="row no-gutters">
        <div class="col-5">
          <label>เพศ (Gender)<abbr class="slds-required" title="required">* </abbr></label>
        </div>
        <div class="col-7">
          <combobox-std
            v-model="regisData.Gender__c"
            v-validate="'required'"
            data-vv-as="เพศ (Gender)"
            data-vv-value-path="textInputValue"
            name="Gender__c"
            :error="errors.has('Gender__c')"
            :options="genderList"
            label=""
          >
            <template #error>
              <span>{{ errors.first('Gender__c') }}</span>
            </template>
          </combobox-std>
        </div>
      </div>

      <div class="row no-gutters">
        <div class="col-5">
          <label>เบอร์โทร (Phone Number)</label>
        </div>
        <div class="col-7">
          <input-text-std
            v-model="phoneNumber"
            name="PhoneNo__c"
            disabled
          >
            <template #error>
              <span>{{ errors.first('PhoneNo__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>

      <div class="row no-gutters">
        <div class="col-12">
          <label>กรุณาเลือกผลิตภัณฑ์ของแอ็บบอทที่คุณสนใจมากที่สุด<abbr class="slds-required" title="required">* </abbr></label>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <combobox-std
            v-model="regisData.Preferred_Product__c"
            v-validate="'required'"
            data-vv-as="ผลิตภัณฑ์"
            data-vv-value-path="textInputValue"
            name="Preferred_Product__c"
            :error="errors.has('Preferred_Product__c')"
            :options="preferredProductList"
            label=""
          >
            <template #error>
              <span>{{ errors.first('Preferred_Product__c') }}</span>
            </template>
          </combobox-std>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <label>เหตุผลที่คุณสนใจและอยากทดลองผลิตภัณฑ์ดังกล่าว<abbr class="slds-required" title="required">* </abbr></label>
        </div>
      </div>
      <div class="row no-gutters">
        <div class="col-12">
          <input-text-std
            v-model="regisData.Remark__c"
            v-validate="'required'"
            data-vv-as="เหตุผลที่คุณสนใจ"
            :error="errors.has('Remark__c')"
            name="Remark__c"
          >
            <template #error>
              <span>{{ errors.first('Remark__c') }}</span>
            </template>
          </input-text-std>
        </div>
      </div>
    </div>
    <v-expansion-panels v-model="panel">
      <v-expansion-panel>
        <v-expansion-panel-header>
          <label>ที่อยู่ในการจัดส่ง<abbr class="slds-required" title="required">* </abbr></label>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <!-- <div class="container" style="color: black;"> -->
            <input-text-std
              v-model="regisData.Address__c"
              v-validate="'required'"
              data-vv-as="ที่อยู่"
              :error="errors.has('Address__c')"
              placeholder="ที่อยู่"
              name="Address__c"
              :required="true"
            >
              <template #error>
                <span>{{ errors.first('Address__c') }}</span>
              </template>
            </input-text-std>
            <input-text-std
              v-model="regisData.AdrSoi__c"
              v-validate="'required'"
              data-vv-as="ซอย"
              :error="errors.has('AdrSoi__c')"
              placeholder="ซอย"
              name="AdrSoi__c"
              :required="true"
            >
              <template #error>
                <span>{{ errors.first('AdrSoi__c') }}</span>
              </template>
            </input-text-std>
            <input-text-std
              v-model="regisData.AdrRoad__c"
              v-validate="'required'"
              data-vv-as="ถนน"
              :error="errors.has('AdrRoad__c')"
              placeholder="ถนน"
              name="AdrRoad__c"
              :required="true"
            >
              <template #error>
                <span>{{ errors.first('AdrRoad__c') }}</span>
              </template>
            </input-text-std>

            <div class="form-group auto-address ">
              <thai-address-input type="subdistrict"
                                  v-model="regisData.AdrSubDistrinct__c"
                                  @selected="onSelected"
                                  placeholder="แขวง/ตำบล" 
                                  :required="true"
                                  v-validate="'required'"
                                  data-vv-as="แขวง/ตำบล"
                                  data-vv-value-path="inputValue"
                                  :error="errors.has('AdrSubDistrinct__c')"
                                  name="AdrSubDistrinct__c"></thai-address-input>
            </div>
            <div v-show="errors.has('AdrSubDistrinct__c')" class="slds-form-element__help" style="color: red;">
              <span>{{ errors.first('AdrSubDistrinct__c') }}</span>
            </div>
            <div class="form-group auto-address ">
              <thai-address-input type="district"
                                  v-model="regisData.AdrDistrinct__c"
                                  @selected="onSelected"
                                  placeholder="เขต/อำเภอ" 
                                  :required="true"
                                  v-validate="'required'"
                                  data-vv-as="เขต/อำเภอ"
                                  data-vv-value-path="inputValue"
                                  :error="errors.has('AdrDistrinct__c')"
                                  name="AdrDistrinct__c"></thai-address-input>
            </div>
            <div v-show="errors.has('AdrDistrinct__c')" class="slds-form-element__help" style="color: red;">
              <span>{{ errors.first('AdrDistrinct__c') }}</span>
            </div>
            <div class="form-group auto-address ">
              <thai-address-input type="province"
                                  v-model="regisData.AdrProvince__c"
                                  @selected="onSelected"
                                  placeholder="จังหวัด" 
                                  :required="true"
                                  v-validate="'required'"
                                  data-vv-as="จังหวัด"
                                  data-vv-value-path="inputValue"
                                  :error="errors.has('AdrProvince__c')"
                                  name="AdrProvince__c"></thai-address-input>
            </div>
            <div v-show="errors.has('AdrProvince__c')" class="slds-form-element__help" style="color: red;">
              <span>{{ errors.first('AdrProvince__c') }}</span>
            </div>
            <div class="form-group auto-address ">
              <thai-address-input type="postalCode"
                                  v-model="regisData.AdrZipCode__c"
                                  @selected="onSelected"
                                  placeholder="รหัสไปรษณีย์" 
                                  :required="true"
                                  v-validate="'required'"
                                  data-vv-as="รหัสไปรษณีย์"
                                  data-vv-value-path="inputValue"
                                  :error="errors.has('AdrZipCode__c')"
                                  name="AdrZipCode__c"></thai-address-input>
            </div>
            <div v-show="errors.has('AdrZipCode__c')" class="slds-form-element__help" style="color: red;">
              <span>{{ errors.first('AdrZipCode__c') }}</span>
            </div>
          <!-- </div> -->
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <div class="container text-condition-submit">
      <div class="row">
        <div class="col-1">
          <checkbox-std
            v-model="checkConfirmCondition"
            :error="!checkConfirmCondition"
          >
          </checkbox-std>
        </div>
        <div class="col-11">
          <span v-html="detailConditionSubmit !== '' ? conditionDefault : conditionDefault"></span>
        </div>
      </div>
      <div class="row">
        <div class="col-12" style="padding-top: 0px;">
          <v-btn
            @click="submit()"
            class="thaiSansExtraBold"
            rounded color="primary" dark
            :elevation="2"
            :disabled="!checkConfirmCondition"
          >
            ส่งคำตอบ
          </v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['setNumPage', 'setNumPage', 'setOverlay', 'setOtpNumber', 'setTextError', 'setDialog', 'mid'],
  data () {
    return {
      panel: null,
      regisData: {
        Name__c: '',
        Surname__c: '',
        Gender__c: '',
        PhoneNo__c: '',
        Preferred_Product__c: '',
        Remark__c: '',
        Address__c: '',
        AdrSoi__c: '',
        AdrRoad__c: '',
        AdrSubDistrinct__c: '',
        AdrDistrinct__c: '',
        AdrProvince__c: '',
        AdrZipCode__c: '',
        MID__c: '',
        CR__c: 'Line'
      },
      conditionDefault: 'ผู้เข้าร่วมกิจกรรมยินดีให้บริษัท แอ๊บบอต ลาบอแรตอริส จำกัด' +
      '("บริษัทฯ“) ติดต่อทางโทรศัพท์ ข้อความ อีเมล และไปรษณีย์ เพื่อให้' +
      'คำปรึกษาด้านโภชนาการ คำแนะนำเกี่ยวกับผลิตภัณฑ์ โปรโมชั่นพิเศษต่างๆ โดยบริษัทฯ จะเก็บรักษาข้อมูลของผู้เข้าร่วมกิจกรรมไว้เป็นความลับและไม่เผยแพร่ต่อบุคคลอื่น<br><br>' +
      'หมายเหตุ: ท่านสามารถแจ้งยกเลิกการติดต่อจากทางบริษัทฯ ได้' +
      'ตลอดเวลา โดยโทรผ่านทาง Abbott Cares 0-2252-2448 ในเวลาทำการ (08:30 น.- 17:30 น.) จันทร์-ศุกร์ เว้นวันหยุดราชการ',
      checkConfirmCondition: false
    }
  },
  mounted () {
    this.panel = 0
    this.$nextTick(() => {
      this.panel = null
    })
  },
  computed: {
    phoneNumber () { return this.$store.state.phoneNumber },
    genderList () { return this.$store.state.ddlGender },
    preferredProductList () { return this.$store.state.ddlPrefferedProduct },
    detailConditionSubmit () { return this.$store.state.detailConditionSubmit }
  },
  methods: {
    onSelected (address) {
      // this.subdistrict = address.subdistrict
      // this.district = address.district
      // this.province = address.province
      // this.postalCode = address.postalCode
      this.regisData.AdrSubDistrinct__c = address.subdistrict
      this.regisData.AdrDistrinct__c = address.district
      this.regisData.AdrProvince__c = address.province
      this.regisData.AdrZipCode__c = address.postalCode
    },
    submit () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.regisData.PhoneNo__c = this.phoneNumber
          this.regisData.MID__c = this.mid
          this.setOverlay(true)
          this.$store.dispatch('saveSurveyForm', this.regisData).then(resolve => {
            if (resolve.status) {
              this.setOverlay(false)
              this.setNumPage(4)
            } else {
              this.setOverlay(false)
              this.setTextError('เกิดข้อผิดพลาด กรุณาติดต่อผู้ดูแลระบบ')
              if (typeof resolve.message !== 'undefined' && resolve.message !== '') {
                this.setTextError(resolve.message)
              }
              this.setDialog(true)
            }
          })
        } else {
          this.panel = 0
        }
      })
    }
  }
}
</script>

<style scoped>
  label {
    color: black;
  }
  /deep/ .slds-input {
    line-height: 1.5rem;
    min-height: 1.5rem;
  }
  /deep/ .v-item-group {
    position: unset;
  }
  /deep/ .v-expansion-panel-header {
    padding: 0 12px;
    font-size: .8125rem;
    min-height: 20px;
    width: auto;
  }
  /deep/ .v-expansion-panel-header__icon {
    border: solid 1px #e0e0e0;
    border-radius: 45px;
  }
  /deep/ .v-expansion-panel--active>.v-expansion-panel-header {
    min-height: 20px;
  }
  /deep/ .v-icon.v-icon {
    font-size: 15px;
  }
  /deep/ .theme--light.v-expansion-panels .v-expansion-panel-header .v-expansion-panel-header__icon .v-icon {
    color: rgb(4 129 182);
  }
  /deep/ .v-expansion-panel-content__wrap {
    padding: 0 12px;
  }
  .auto-address {
    border: 1px solid var(--slds-c-input-color-border,var(--sds-c-input-color-border,#c9c9c9));
    border-radius: var(--slds-c-input-radius-border,var(--sds-c-input-radius-border,.25rem));
    /* font-weight: bold;
    font-size: 1rem; */
    /* padding-left: var(--slds-c-input-spacing-horizontal-start,var(--sds-c-input-spacing-horizontal-start,.75rem)); */
    min-height: 1.5rem;
    margin-bottom: 2px;
  }

  .auto-address /deep/ input.vth-addr-input {
    /*font-family: 'ThaiSans Neue Extra Bold', Helvetica, Arial, sans-serif;*/
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding-left: 1.5rem !important;
    box-shadow: none;
    border: none !important;
  }
  .thai-address-input /deep/ input {
    width: 100%;
    outline: #8ecef0;
    padding-left: 12px !important;
    padding-right: 0.75rem;
  }
  .thai-address-input /deep/ .suggestion-list {
    color: black;
    font-size: 0.8rem;
  }
  .theme--dark.v-btn.v-btn--disabled.v-btn--has-bg {
    background-color: #f3f3f3 !important;
  }
  .theme--dark.v-btn.v-btn--disabled {
    color: darkgray !important;
  }
  .text-condition-submit {
    color: black;
    line-height: 15px;
  }
  /deep/ .theme--dark.v-btn.v-btn--has-bg {
    border: 1px solid white!important;
  }
</style>


